import React from "react"
import Segment from './segment'
import {xz,Navigation} from "../../../../../index"
import {observer} from 'mobx-react'

@observer
class DropDownGroup extends React.Component {

  constructor(props) {
    super(props)
  }


  renderItem(params){
    return <div style={{height:"9rem",backgroundColor:"#fff"}}>{params}</div>
  }


  showPopover(e){
    this.props.store.headerPopoverConfig = {
      target:e.target,
      direction:"bottom"
    };
  }
 

  render() {
    return (<xz.DropDownGroup 
      renderItem={this.renderItem.bind(this)}
      selectedKey={this.props.store.dropDownGroupSelectedKey} className='weibo-header'>
          <span className='weibo-main-header-btn'></span>
          <Segment store={this.props.store}/>
        <span className='weibo-main-header-btn' onClick={this.showPopover.bind(this)}></span>
      </xz.DropDownGroup>);
  }
}
export default DropDownGroup;



