import React from "react"
import {observer} from 'mobx-react'

import {xz,style,shallowEqual,Navigation} from "../../../../index"

@observer
class TabBar extends React.Component {

  constructor(props) {
    super(props)
  }


 
  tabbarChange(params){
    var key = params.itemInstance.props.url||params.selectedKey;
    this.props.pagemanager.replaceGo(key);
  }

  showPage(){
     this.props.store.showPageConfig = {key:"MORE",cache:false,showBackCover:false,direction:"fadein"};
  }



  render() {
    return (
    	 <xz.Segment selectedKey={this.props.store.tabSelectedKey} 
    	 onChange={this.tabbarChange.bind(this)} className="weibo-home-tabbar">
          <xz.Segment.Item key='home/weibo'></xz.Segment.Item>
          <xz.Segment.Item key='home/message'></xz.Segment.Item>
          <div onClick={this.showPage.bind(this)} className='weibo-tabbar-btn'></div>
          <xz.Segment.Item key='home/discover'></xz.Segment.Item> 
          <xz.Segment.Item key='home/me'></xz.Segment.Item> 
         </xz.Segment>);
  }
}
export default TabBar;



